@import '../../assets/css/variable.less';

.dip-box {
  border-top: 1px solid @divider_color ;

  .dip-box-info {
    width: 1140px;
    margin: 0 auto;
    margin-top: 96px;
    font-weight: normal;
    a{
      color: @button_outline_color;
    }
    .dip-info {
      height: 624px;
      border: 1px solid @divider_color;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
      .dip-info-title {
        margin-top: 36px;
        padding-left: 36px;

        .dip-title {
          font-size: 32px;
          color: #FFFFFF;
        }

        .dip-des {
          width: 1035px;
          margin-top: 10px;
        }
      }

      .dip-options {
        margin-top: 50px;

        .dip-options-checked {
          margin-top: 16px;
          width: 1138px;
          height: 82px;
          border-radius: 4px;
          border: 1px solid @divider_color;
          display: flex;
          justify-content: space-between;
          background: rgba(85, 119, 253, 0.1);

          .checkbox {
            width: 150px;
            border-right: 1px solid @divider_color;
            display: flex;
            align-items: center;
            justify-content: center;

            label {
              font-size: 32px;
              font-weight: 600;
              color: @whiteFontColor;
              margin-left: 10px;
              display: flex;
              align-items: center;
              cursor: pointer;
            }

            
            input[type="radio"] {
              position: absolute; 
              clip: rect(0,0,0,0);
          }

            input[type="radio"]+label::before {
              content: '';
              display: inline-block;
              width: 22px;
              height: 22px;
              border-radius: 50%;
              border: 1px solid #FFFFFF;
              margin-right: 13px;
            }
            input[type="radio"]:checked + label::before {
              background-clip: content-box; 
              background-color: #5577FD;
              width: 14px;
              height: 14px;
              padding: 4px;
              border: 1px solid #5577FD;
          }
          }

          .option-describe {
            width: 659px;
            margin-left: 45px;
            display: flex;
            align-items: center;
            color: @whiteFontColor;
            font-size: 14px;
          }

          .peng {
            width: 231px;
            border-left: 1px solid @divider_color;
            display: flex;
            align-items: center;
            padding-left: 26px;
            padding-right: 26px;
            .peng-box{
              width: 100%;
              color: @whiteFontColor;
              .peng-bg{
                background: #5577FD;
                height: 16px;
                margin-bottom: 5px;
              }
              .dip-one-one{
                width: 31%;
              }
              .dip-one-two{
                width: 69%;
              }
              .dip-one-three{
                width: 0;
              }
              .dip-two-one{
                width: 9.02956%;
              }
              .dip-two-two{
                width: 90.9704%;
              }
              .dip-two-three{
                width: 0;
              }
            }
            
          }
        }
        .vote-button{
          display: flex;
          justify-content: center;
          margin-top: 64px;
        }
      }
    }
    .dip-two-info{
      height: 670px;
    }
    .voting-rules{
      margin-top: 48px;
      width: 1140px;
      height: 302px;
      border: 1px solid @divider_color;
      padding-top: 38px;
      padding-left: 36px;
      background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
      .voting-rules-title{
        font-size: 32px;
        color: #FFFFFF;
      }
      .voting-info{
        margin-top: 25px;
        width: 1029px;
        font-size: 14px;
      }
    }
    .dip-two-rules{
      height: 378px;
    }
  }
}
@media only screen and (min-width : 1089px) and (max-width:1140px) {
  .dip-box {
    .dip-box-info {
      width: 1089px;
      .dip-info {
        .dip-info-title {
          .dip-des {
            width: 992px;
          }
        }
        .dip-options {
          .dip-options-checked {
            width: 1087px;
          }
        }
      }
      .voting-rules{
        width: 1089px;
      }
    }
  }
}
@media only screen and (min-width : 760px) and (max-width:1089px) {
  .dip-box {
    .dip-box-info {
      width: 712px;
      .dip-info {
        .dip-info-title {
        padding-left: 22px;
          .dip-des {
            width: 658px;
          }
        }
        .dip-options {
          .dip-options-checked {
            width: 710px;
          }
        }
      }
      .voting-rules{
        width: 712px;
        padding-left: 22px;
        height: 369px;
        .voting-info{
          width: 670px;
        }
      }
    }
  }
}
@media only screen and (max-width : 760px) {
  .dip-box {
    .dip-box-info {
      width: 326px;
      .dip-info {
        height: 100%;
        border: 0;
        .dip-info-title {
          padding-left: 22px;
          .dip-des {
            width: 298px;
          }
        }
        .dip-options {
          .dip-options-checked {
            display: block;
            width: 324px;
            height: 311px;
            .checkbox{
              border: 0;
              width: 100%;
              height: 82px;
              display: block;
              padding-left: 12px;
              line-height: 82px;
              border-bottom: 1px solid @divider_color;
            }
            .option-describe{
              width: 324px;
              margin: 0;
              height: 147px;
              padding-left: 22px;
            }
            .peng{
              width: 100%;
              border: 0;
              height: 82px;
              border-top: 1px solid @divider_color;
              display: flex;
              align-items: center;
            }
          }
          .vote-button{
            margin-bottom: 70px;
            div{
              width: 208px;
            }
          }
        }
      }
      .voting-rules{
        width: 326px;
        height: 502px;
        padding-left: 8px;
        padding-right: 9px;
        .voting-info{
          width: 306px;
        }
      }
    }
  }
}
